.header {
    width: 100%;
    display: flex;
    justify-content: space-between;
    align-items: center;
    position: relative;
    z-index: 2;
    padding: 10px 20px 0;
  }
  
  .header-left {
    display: flex;
    align-items: center;
  }
  
  .header-title {
    display: flex;
    align-items: center;
    gap: 10px;
    text-decoration: none;
    color: inherit;
  }
  
  .header-title .logo {
    width: 40px;
    height: 40px;
  }
  
  .header-right {
    display: flex;
    align-items: center;
    gap: 10px;
  }

  .nav-links {
    display: flex;
    align-items: center;
    font-size: 14px;
  }

  .nav-link {
    color: #fff;
    padding: 6px 6px;
    text-decoration: none;
    border-radius: 8px;
    transition: background-color 0.15s ease, opacity 0.15s ease;
  }

  .nav-link:nth-child(3) {
    margin-right: 10px;
  }

  .light .nav-link {
    color: #000;
  }

  .nav-link:hover {
    background: #476629;
    
  }

  .light .nav-link:hover {
    background: #5b7e36;
    color: white;
  }
  
  .header h2 {
    font-family: "New Rocker", system-ui;
    font-weight: 400;
    font-style: normal;
  }
      
  
  .mode-toggle {
    border-radius: 8px;
    padding: 6px 12px;
    font-weight: 600;
    cursor: pointer;
    border: 1px solid #fff;
    background-color: transparent;
    color: #fff; 
    text-decoration: none;
    z-index: 3;
    transition: all 0.2s ease;
  }
  
  .light .mode-toggle {
    border: 1px solid #000; 
    color: #000;
  }
  
  .mode-toggle:hover {
    opacity: 0.8;
  }

  .hamburger {
    position: relative;
    width: 32px;
    height: 32px;
    background: transparent;
    cursor: pointer;
    display: none;
    align-items: center;
    justify-content: center;
    padding: 0;
    border: none;
  }

  .hamburger .line {
    position: absolute;
    width: 22px;
    height: 1px;
    background: currentColor;
    color: inherit;
    transition: transform 0.2s ease, opacity 0.2s ease;
  }

  .hamburger .line1 { transform: translateY(-6px); }
  .hamburger .line2 { transform: translateY(0); }
  .hamburger .line3 { transform: translateY(6px); }

  .hamburger.open .line1 { transform: rotate(45deg); }
  .hamburger.open .line2 { opacity: 0; }
  .hamburger.open .line3 { transform: rotate(-45deg); }

  .mobile-menu {
    position: absolute;
    top: 58px;
    right: 16px;
    display: flex;
    flex-direction: column;
    gap: 10px;
    padding: 12px;
    min-width: 180px;
    background: #000;
    border: 1px solid #fff;
    border-radius: 10px;
    z-index: 20;
  }

  .light .mobile-menu {
    background: #fff;
    border: 1px solid #000;
  }

  .mobile-link {
    color: inherit;
    text-decoration: none;
    padding: 6px 6px;
    border-radius: 8px;
    display: block;
  }

  .mobile-link:hover {
    background: #476629;
  }

  .light .mobile-link:hover {
    background: #5b7e36;
    color: white;
  }

  .mobile-mode-toggle {
    border-radius: 8px;
    padding: 8px 10px;
    font-weight: 600;
    cursor: pointer;
    border: 1px solid #fff;
    background-color: transparent;
    color: #fff;
  }

  .light .mobile-mode-toggle {
    border: 1px solid #000;
    color: #000;
  }

  /* Hamburger theme colors */
  .hamburger { color: #fff; }
  .light .hamburger { color: #000; }

  @media (max-width: 768px) {
    .nav-links { display: none; }
    .hamburger { display: inline-flex; }
  }